import React,  {useState} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import ListSubheader from '@material-ui/core/ListSubheader';
import IconButton from '@material-ui/core/IconButton';
import InfoIcon from '@material-ui/icons/Info';
import ItemsCard from '../ItemsCard/ItemsCard';
import '../play.css';
import { Card } from '@material-ui/core';

export const PlayCard = ({close,url})=>{
    return (
      <div className='play' onClick={()=>{close(false)}}>
      <Card className='content'>
      <button className='close-btn' onClick={()=>{close(false)}}>X</button>
        <iframe width="98%" height="98%" src={url} title="YouTube video player" frameborder="5" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
      </Card>
      </div>
    )
  }

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    backgroundColor: 'white',
    boxShadow: "-1px 2px #80808080",
    borderRadius: "25px",
    border : "1px solid gray"
   
    
  },
  gridList: {
    maxwidth: 520,
    maxheight: 800,
    justifyContent: 'center',
    padding: "2%",
  },
  icon: {
    color: 'rgba(255, 255, 255, 0.54)',
  },
}));

/**
 * The example data is structured as follows:
 *
 * import image from 'path/to/image.jpg';
 * [etc...]
 *
 * const tileData = [
 *   {
 *     img: image,
 *     title: 'Image',
 *     author: 'author',
 *   },
 *   {
 *     [etc...]
 *   },
 * ];
 */
export default function TitlebarGridList({items}) {
  const classes = useStyles();
  const [play,setplay] = useState(false);
  const [url,setUrl] = useState('');
  const playHandel = (url)=>{
    setUrl(url);
    setplay(true)
  }
  return (
    <div className={classes.root}>
        {play?<PlayCard close={setplay} url={url}></PlayCard>:<></>}
      <GridList cellHeight={180} cols={0} className={classes.gridList}>
        {items.map((item) => (
          <GridListTile key={item.imgName} style={{ height: 'auto' }}>
          <ItemsCard videoName={item.videoName} imageName={item.imgName} onClick={()=>playHandel(item.URL)}/>
          </GridListTile>
        ))}
      </GridList>
    </div>
  );
}
