import React,{ useState } from 'react';
import { BrowserView, MobileView } from "react-device-detect";
import MuiLayout from './Matrial-Ui/Layout/MuiLayout'
import DuiLayout from './Matrial-Ui/Layout/DuiLayout'
import AboutUs from './Pages/AboutUs/Aboutus';
import Contact from './Pages/Contact/Contact';
import Home from './Pages/Home/Home';
import Learn from './Pages/Learn/Learn';
import HomeIcon from '@material-ui/icons/Home';
import CollectionsIcon from '@material-ui/icons/Collections';
import StorefrontIcon from '@material-ui/icons/Storefront';
import LocalLibraryIcon from '@material-ui/icons/LocalLibrary';
import InfoIcon from '@material-ui/icons/Info';
import ContactPhoneIcon from '@material-ui/icons/ContactPhone';

const icons = {
  'Home': <HomeIcon />,
  'Gallery': <CollectionsIcon />,
  'Shop': <StorefrontIcon />,
  'Learn': <LocalLibraryIcon />,
  'AboutUs': <InfoIcon />,
  'Contact': <ContactPhoneIcon />,
}
const pages = {
  'Home': <Home />,
  'Gallery': <div> My Art Collection </div>,
  'Shop': <div> Make it yours... just buy it.</div>,
  'Learn': <Learn />,
  'AboutUs': <AboutUs />,
  'Contact': <Contact />,
}

function App() {
  
 
  const currectPageState = useState('Home');
  return (
    <React.Fragment>
    <BrowserView>
    <DuiLayout pages={pages} pageIcons={icons} currentPage={currectPageState}/>
    </BrowserView>
    <MobileView>
    <MuiLayout pages={pages} pageIcons={icons} currentPage={currectPageState}/>
    </MobileView>
  </React.Fragment>
  )
}
export default App;
