import React, {useState} from 'react';
import './Learn.css';
import data from '../../Data/data.json';
import ItemsCard from './ItemsCard/ItemsCard';
import TitlebarGridList from './GridList/GridList';
import { Card } from '@material-ui/core';


const  Details = () =>{ 
   return (Object.keys(data.Learn.videos).map((catagery,key)=>
   <div key={catagery+key}>
   <h3 id={catagery}>{catagery}</h3>
   {<TitlebarGridList items={Object.values(data.Learn.videos[catagery])}/>}
   </div>))
}


class Learn extends React.Component {

    render(){
    return (
        <div className='learn' id="Learn">
            <div className='training'>
                <h1>{data.Learn.Title[0]}</h1>
                <h3>{data.Learn.Title[1]}</h3>
                <h4>{data.Learn.Title[2]}</h4>
            </div>
            <Details/>
        </div>
    )
}
}

export default Learn;