import React from 'react';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';


const useStyles = makeStyles((theme) => ({
    headerAd: {
        padding: theme.spacing(0),
        textAlign: 'center',
        color: theme.palette.text.primary,
        backgroundColor: '#c8e2ff'
      },
}))

function ScrollPanel () {
  const theme = useTheme();
  const classes = useStyles(theme);
    return (
        <Paper className={classes.headerAd}>
        <center behavior="alternate" scrollamount="3">
          <dl >
        <dt key='0' style={{display: 'inline-block'}}><p>* * * * * * * * *</p></dt>
        <dt key='1' style={{display: 'inline-block'}}><a href="https://www.youtube.com/channel/UCYCHGSUh5CjlV8SAqZfRyyQ" target="_blank" rel="noreferrer">   Youtube chennal link</a></dt>
        <dt key='2' style={{display: 'inline-block'}}><p>* * * * * * * * *</p></dt>
        <dt key='3' style={{display: 'inline-block'}}><a href="https://www.youtube.com/watch?v=6CTiI9emNSM" rel="noreferrer" target="_blank"> Latest Training Updated</a></dt>
        <dt key='4' style={{display: 'inline-block'}}><p>* * * * * * * * *</p></dt>
        </dl>
        </center>
      </Paper>
    )
}

export default ScrollPanel;