import React from 'react';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import data from '../../../Data/data.json';
import BrushIcon from '@material-ui/icons/Brush';


const useStyles = makeStyles((theme) => ({
    leftPages: {
        padding: theme.spacing(1),
        textAlign: 'left',
        backgroundColor: '#fbf5be',
        //backgroundImage: 'linear-gradient( 180deg, #def16f 10%, #ffacac 50%, #def16f 100%)',
        color: theme.palette.text.primary,
        minHeight: '350px',
        position: '-webkit-sticky',
        position: 'sticky',
        top: '5px',
        declaration: 'none',
        border : "5px solid #fff"
      },
}))

function LeftPanel ({currenPage,pageIcons}) {
  const theme = useTheme();
  const classes = useStyles(theme);
    return (
            <Paper className={classes.leftPages}>
              
              
              <p style={{ textAlign: 'center',}}><a href={"#"+currenPage} style={{textDecoration:'none'}}>{pageIcons[currenPage]}</a><br/><strong>{currenPage} Index :</strong></p>
              {data[currenPage].Indexs!==undefined?
              <dl>
            {data[currenPage].Indexs.map((index,key)=>{
              return<dt key={currenPage+key}><a href={"#"+index} style={{textDecoration:'none',fontSize:'12px'}}>{index}<BrushIcon/></a></dt>
            })}
            </dl>
            :<React.Fragment/>}
            </Paper>
    )
}

export default LeftPanel;