import React from 'react';
import './Home.css';
import data from '../../Data/data.json';
  


const  Details = () =>{
    const para = (topic)=>topic.Content.map((text,k)=><p key={k}>{text}</p>)
    const items = (topic)=>topic.Items?topic.Items.map((item,i)=><li key={"items"+i}>{item}</li>):<React.Fragment/>
    const MyListItem = (topic)=><ol>{items(topic)}</ol>
   return (data.Home.Topics.map((topic,key)=>
   <div key={key}>
   <h3 id={topic.Title}>{topic.Title}</h3>
   {para(topic)}
   <MyListItem topic={topic}/>
   </div>))
}


class Home extends React.Component {
    
    render(){
    return (
        <div className='home' id="Home">
            <div className='welcome'>
                <h1>{data.Home.Title[0]}</h1>
                <h3>{data.Home.Title[1]}</h3>
                <h4>{data.Home.Title[2]}</h4>
                <img src={process.env.PUBLIC_URL+data.Home.image} alt='balaji' className="balaji"/>
            </div>
            <Details/>
        </div>
    )
}
}

export default Home;